//活动状态管理
import * as types from '../types'
const state = {
    httpLoading : false,
};

const getters = {}
const actions = {}

const mutations = {
    [types.HTTP_LOADING](state,payload) {
        state.httpLoading = payload.httpLoading
    },
};

export default {
    state,
    getters,
    mutations,
    actions
}


