//页面切换等状态管理
import * as types from '../types'
const state = {
    token: null,
};

const getters = {}
const actions = {}

const mutations = {
    [types.USER_TOKEN](state,payload) {
        state.token = payload.token
    },
};

export default {
    state,
    getters,
    mutations,
    actions
}



