import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

import page404 from '@/views/404'

Vue.use(Router)

window.vueHistory = 0;

const router = new Router({
    routes: [
        { path: '*',
          component : function (resolve) {
              require(['@/views/404.vue'], resolve)
          }
        },
        {
            //活动详情
            path: '/activity/detail',
            name: 'activity',
            component : function (resolve) {
                require(['@/views/activity.vue'], resolve)
            }
        },
        {
            //活动详情线下
            path: '/activity/offline',
            name: 'offline_activity',
            component : function (resolve) {
                require(['@/views/offline_activity.vue'], resolve)
            }
        },
        {
            //活动详情视频
            path: '/activity/video',
            name: 'video_activity',
            component : function (resolve) {
                require(['@/views/video_activity.vue'], resolve)
            }
        },
        {
            //活动详情问卷
            path: '/activity/questionnaire',
            name: 'questionnaire_activity',
            component : function (resolve) {
                require(['@/views/questionnaire_activity.vue'], resolve)
            }
        },
        {
            //参加活动人列表
            path: '/activity/joinmembers',
            name: 'join_activity_member_list',
            component : function (resolve) {
                require(['@/views/join_activity_member_list.vue'], resolve)
            }
        },
        {
            //线下活动提交
            path: '/activity/commitoffline',
            name: 'commit_offline_activity',
            component : function (resolve) {
                require(['@/views/commit_offline_activity.vue'], resolve)
            }
        },
        {
            //demo
            path: '/',
            name: 'HelloWorld',
            component : function (resolve) {
                require(['@/components/HelloWorld.vue'], resolve)
            }
        },
        {
            //地图
            path: '/map/gaode',
            name: 'gaodeMap',
            component : function (resolve) {
                require(['@/components/GaoDeMap.vue'], resolve)
            }
        }
    ]
})

const history = window.sessionStorage;
history.clear()
let historyCount = history.getItem('count') * 1 || 0;
history.setItem('/', 0);

router.beforeEach((to, from, next) => {
    const toIndex = history.getItem(to.path);
    const fromIndex = history.getItem(from.path);

    if (toIndex) {
        if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
            store.commit('UPDATE_DIRECTION', {direction: 'forward'})
        } else {
            store.commit('UPDATE_DIRECTION', {direction: 'reverse'})
        }
    } else {
        ++historyCount;
        history.setItem('count', historyCount);
        to.path !== '/' && history.setItem(to.path, historyCount);
        store.commit('UPDATE_DIRECTION', {direction: 'forward'})
    }
    next()
})

router.afterEach((to, from, next) => {
    store.commit("HISTORY",{num:1});
})

export default router



