

import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import FastClick from 'fastclick'
import router from './router'
import Native from './native'
import Directive from './directive'
import Filter from './filter'
import store from './store'
import * as constant from './constant'

Vue.use(Vuex);
Vue.use(AlertPlugin);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);

new Vue({
el: '#app',
router,store,
template: '<App/>',
components: { App }
})


FastClick.attach(document.body);


if(constant.PRODUCTION_ENV == 2){
require('es6-promise').polyfill();
}


if(constant.PRODUCTION_ENV == 0){
var time = 0
if(constant.isIOS) time = 50;
setTimeout( () => {
Native.method("getPage",function (response) {
response = JSON.parse(response);
router.push({path:"/activity/detail",query:{
id:response.id
}});

localStorage.token = response.token;
})
},time)
}

