import { mapState } from 'vuex'
import Vue from 'vue'

export default {
  name: 'app',
    computed:{
        ...mapState({
            direction: state => state.routerModule.direction,
            loading: state => state.axiosModule.httpLoading
        })
    },
    watch:{
      loading:(newValue,oldValue)=>{
          if(newValue){
              Vue.$vux.loading.show({
                  text: 'Loading'
              })
          }else{
              Vue.$vux.loading.hide();
          }
      }
    }
}



