//活动状态管理
import * as types from '../types'
const state = {
    activityStatus: null,
    join:0,
};

const getters = {}
const actions = {}

const mutations = {
    [types.UPDATE_ACTIVITY_STATUS](state,payload) {
        state.activityStatus = payload.activityStatus
    },
    [types.UPDATE_JOIN](state,payload) {
        state.join = payload.join
    },
};

export default {
    state,
    getters,
    mutations,
    actions
}


