//页面切换等状态管理
import * as types from '../types'
const state = {
    direction: 'forward',
    history:0
};

const getters = {}
const actions = {}

const mutations = {
    [types.UPDATE_DIRECTION](state,payload) {
        state.direction = payload.direction
    },
    [types.HISTORY](state,payload) {
        state.history = state.history + payload.num
    },

};

export default {
    state,
    getters,
    mutations,
    actions
}



