//存放了和原生安卓和ios交互的方法
import * as constant from '../constant'

import hybrid from './hybrid'

if(constant.isIOS){
    function setupWebViewJavascriptBridge(callback) {
        if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
        if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
        window.WVJBCallbacks = [callback];
        var WVJBIframe = document.createElement('iframe');
        WVJBIframe.style.display = 'none';
        WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
        document.documentElement.appendChild(WVJBIframe);
        setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0)
    }

    //native调用js
    setupWebViewJavascriptBridge(function(bridge) {

        for(var i = 0;i<hybrid.length;i++){
            var item = hybrid[i];
            bridge.registerHandler(item.name, item.methodIOS)
        }

    })
}


export default {
    method(methodName,fun,fields){
        if(constant.isAndroid) {
            if(fields&&fields.length > 0){
                if(fields.length == 1) fun(window.native[methodName](fileds[0]));
                if(fields.length == 2) fun(window.native[methodName](fileds[0],fileds[1]));
                if(fields.length == 3) fun(window.native[methodName](fileds[0],fileds[1],fileds[2]));
                if(fields.length == 4) fun(window.native[methodName](fileds[0],fileds[1],fileds[2],fileds[3]));
            } else{
                fun(window.native[methodName]());
            }

        }else if (constant.isIOS){
            try {
                WebViewJavascriptBridge.callHandler(methodName, fields, fun);
            }catch (e) {
                alert(e);
            }

        }
    }
}



