/*** IMPORTS FROM imports-loader ***/
var baseEnv = 0;

import * as LOGLEVEL from './loglevel.js'

export const LOG_LEVEL = LOGLEVEL.DEBUG;
export const PRODUCTION_ENV = baseEnv;    //0是在原生上运行 1 是在手机浏览器上运行  2 是在电脑端浏览器上使用
//获取浏览器类
const ua = navigator.userAgent;
export const isAndroid =PRODUCTION_ENV== 0 && (ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1); //android终端
export const isIOS = PRODUCTION_ENV== 0 && (!!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)); //ios终端

//高德地图key
